@font-face {
  font-family: Roboto;
  font-style: "normal";
  font-weight: 300;
  src: url("Roboto-Thin.2eb18318.ttf");
}

@font-face {
  font-family: Roboto;
  font-style: "normal";
  font-weight: 500;
  src: url("Roboto-Regular.5068caf2.ttf");
}

@font-face {
  font-family: Roboto;
  font-style: "normal";
  font-weight: "bold";
  src: url("Roboto-Bold.61bf53a1.ttf");
}

@font-face {
  font-family: Cantarell;
  font-style: "normal";
  font-weight: "bold";
  src: url("Cantarell-Bold.ac5c3783.ttf");
}

@font-face {
  font-family: Cantarell;
  font-style: "normal";
  src: url("Cantarell-Regular.d9697145.ttf");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #010400;
  background: #fcfcfc;
  font-family: Roboto;
  font-size: 14px;
}

a {
  color: #010400;
  text-decoration: none;
}

h1, h2, h3 {
  font-family: Roboto, sans-serif;
}

body {
  max-width: 70%;
  margin: 0 auto;
  padding: 0 124px;
}

@media (max-width: 840px) {
  body {
    max-width: 100%;
    padding: 0 8%;
  }
}

@media (max-width: 480px) {
  body {
    max-width: 100%;
    padding: 0 4%;
  }
}

body .top-nav {
  height: 90px;
  display: flex;
  position: relative;
}

body .top-nav .brand {
  width: 60px;
  height: 60px;
  flex-grow: 1;
  align-self: center;
}

body .top-nav .brand-avatar {
  height: 100%;
}

body .top-nav .nav-items {
  justify-self: right;
  align-items: center;
  padding: 2em;
  list-style-type: none;
  display: flex;
}

body .top-nav .nav-item {
  flex-grow: 1;
  padding-right: 2em;
}

body .top-nav .nav-item a {
  color: #010400;
  font-family: Roboto, sans-serif;
  font-size: 1.4em;
  text-decoration: underline;
}

body .top-nav .nav-item a:hover {
  color: #465c69;
}

body .top-nav .nav-item:last-child {
  padding: 0;
}

@media (max-width: 480px) {
  body .top-nav .nav-item {
    padding-right: 1em;
  }

  body .top-nav .nav-item a {
    font-size: 1.2em;
  }
}

.title {
  margin: 2em 0;
}

.title h1 {
  text-transform: capitalize;
  font-family: Roboto, sans-serif;
  font-size: 1.7em;
  font-weight: bold;
  line-height: 1.2;
}

blockquote {
  background-color: #cfd9df;
  border-left: 4px solid #465c69;
  margin: 1em 0;
  padding: .01em 1em;
}

img, figure {
  background-color: #cfd9df;
}

img figcaption, figure figcaption {
  border-bottom: 2px solid #465c69;
  padding: 0 .4em .4em;
  font-size: .7em;
}

pre {
  padding: 1em;
  overflow: auto;
}

ul {
  font-family: Cantarell, sans-serif;
  list-style-type: none;
}

li {
  align-items: center;
  margin-bottom: .6em;
  padding: .3em 0;
  line-height: 1.3;
  display: flex;
}

.li-icon {
  content: "";
  width: 88px;
  height: 48px;
  background-position: 0 0;
  background-repeat: no-repeat;
  flex-shrink: 0;
  display: block;
}

.li-icon--blog {
  background-image: url("post.6dd5fefd.svg");
}

.li-icon--talks {
  background-image: url("talk.7e2d06c8.svg");
}

.li-icon--poems {
  background-image: url("poems.21b7f1bb.svg");
  background-size: contain;
}

li.title {
  font-size: 1.2em;
}

li.meta {
  margin-top: .5em;
  font-size: .6em;
}

li.meta, li.meta a {
  color: #465c69;
  font-family: Roboto, sans-serif;
  display: flex;
}

li.meta .meta-item, li.meta a .meta-item {
  border-right: 1px solid #6f6f6fd1;
  padding: 0 1em 0 0;
  line-height: 1;
  display: flex;
}

li.meta .meta-item:last-child, li.meta a .meta-item:last-child {
  border-right: none;
  padding-right: 0;
}

li.meta .tags, li.meta a .tags {
  text-transform: capitalize;
  flex-wrap: wrap;
  display: flex;
}

li.meta .tags a, li.meta a .tags a {
  margin-left: 1em;
}

.content-meta {
  margin-top: .5em;
}

.root .content {
  font-family: Cantarell, sans-serif;
  font-size: 1.5em;
  font-style: normal;
  font-weight: 500;
  line-height: 1.4;
}

.content-meta, .content-meta a {
  color: #465c69;
  font-family: Roboto, sans-serif;
  display: flex;
}

.content-meta .meta-item, .content-meta a .meta-item {
  border-right: 1px solid #6f6f6fd1;
  padding: 0 1em;
  font-size: .8em;
  line-height: 1;
  display: flex;
}

.content-meta .meta-item:last-child, .content-meta a .meta-item:last-child {
  border: none;
  padding-right: 0;
}

.content-meta .date, .content-meta a .date {
  padding: 0 1em 0 0;
}

.content-meta .tags ul, .content-meta a .tags ul {
  flex-wrap: wrap;
  list-style-type: none;
  display: flex;
}

.content-meta .tags ul li, .content-meta a .tags ul li {
  padding-right: 1em;
}

.main-article {
  min-height: 500px;
  font-family: Cantarell, sans-serif;
}

.main-article strong, .main-article b {
  font-weight: bold;
}

.main-article p {
  font-family: inherit;
  font-size: inherit;
  margin: 1em 0;
}

.main-article h3 {
  margin: .4em 0;
}

.main-article a {
  text-decoration: underline;
}

.main-article ul {
  margin-left: 1.4em;
}

.main-article li {
  margin: .7em 0 .7em .7em;
}

.content-footer {
  flex-direction: column;
  justify-content: space-between;
  margin: 6em 0;
  font-family: Roboto, monospace;
  display: flex;
}

.content-footer > nav {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.content-footer > .post-comments {
  margin-top: 3em;
}

.footer-nav {
  width: 40%;
  font-weight: bold;
}

.footer-nav label {
  color: #465c69;
  font-size: .7em;
  display: block;
}

.footer-nav a {
  line-height: 1.4;
  text-decoration: underline;
  display: block;
}

.home {
  display: flex;
}

@media (max-width: 840px) {
  .home {
    flex-direction: column;
  }
}

.home .sidebar {
  width: 450px;
  text-align: center;
  border-right: .2px solid #6f6f6fd1;
  padding: 35px;
  position: relative;
  top: 0%;
  bottom: 0%;
  left: 0;
}

.home .sidebar .name {
  font-family: Roboto;
  font-size: 2em;
  font-weight: bold;
  line-height: 2.6;
}

.home .sidebar .handle {
  font-family: Roboto;
  font-size: 1.3em;
  font-weight: 300;
  line-height: 0;
}

.home .sidebar .quote {
  margin: 45px 0 15px;
  padding: 20px;
  font-family: Roboto;
  font-size: 1.4em;
  font-weight: 300;
  position: relative;
}

.home .sidebar .quote:before {
  content: "";
  width: 14px;
  height: 14px;
  background: url("quote.3fa3b0e1.svg") no-repeat;
  position: absolute;
  top: 0;
  left: 21px;
}

.home .sidebar .social {
  width: 148px;
  margin: 0 auto;
  padding-top: 40px;
  position: relative;
  overflow: hidden;
}

.home .sidebar .social a {
  clear: top;
  float: left;
  width: calc(50% - 20px);
  margin: 10px;
  display: block;
}

.home .sidebar .social span {
  width: 48px;
  height: 48px;
  display: block;
  position: relative;
}

.home .sidebar .social .github {
  background: url("github.d110c599.svg") no-repeat;
}

.home .sidebar .social .stackoverflow {
  background: url("stackoverflow.590d40a2.svg") no-repeat;
}

.home .sidebar .social .rss {
  background: url("rss.92cbf38e.svg") no-repeat;
}

.home .sidebar .social .linkedin {
  background: url("linkedin.90b7fb3e.svg") no-repeat;
}

.home .sidebar .pub-key-qr {
  margin: 55px auto 0;
}

@media (max-width: 990px) {
  .home .sidebar {
    width: 40%;
  }
}

@media (max-width: 840px) {
  .home .sidebar {
    width: 100%;
  }
}

.home .content {
  width: calc(100% - 450px);
  max-width: 872px;
  padding: 40px;
  position: relative;
}

.home .content h2 {
  padding: 10px 0;
  font-family: Roboto, sans-serif;
  font-size: 2.2em;
  font-weight: bold;
}

.home .content .about-me-snippet p {
  align-items: center;
  margin: .8em 0;
  font-family: Cantarell, sans-serif;
  font-size: 1.35em;
  font-weight: 500;
  line-height: 1.5;
}

.home .content .about-me-snippet p a {
  font-weight: bold;
  text-decoration: underline;
}

.home .content .about-me-snippet p a:active {
  color: #465c69;
}

.home .content .hire-status {
  width: 14em;
  height: 2em;
  text-align: center;
  margin: 1em 0 2.4em;
  padding: .2em .4em;
  font-size: 1.4em;
  transition: all .3s;
  position: relative;
  overflow: hidden;
}

.home .content .hire-status__title {
  text-align: center;
  width: 100%;
  padding: .2em;
  line-height: 1;
}

.home .content .hire-status__dialog {
  opacity: 0;
  text-align: left;
  padding: 10px 15px;
  line-height: 1.7;
  transition: opacity;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.home .content .hire-status__dialog__title {
  padding: 0;
  font-size: 1.5em;
}

@media (max-width: 480px) {
  .home .content .hire-status {
    width: 100%;
  }
}

.home .content .hire-status--expanded {
  width: 25em;
  height: 11em;
}

.home .content .hire-status--expanded .hire-status__title {
  display: none;
}

.home .content .hire-status--expanded .hire-status__dialog {
  opacity: 1;
  flex-flow: column;
  transition: opacity 1s ease-out;
}

@media (max-width: 480px) {
  .home .content .hire-status--expanded .hire-status__dialog {
    position: relative;
  }
}

@media (max-width: 480px) {
  .home .content .hire-status--expanded {
    width: 100%;
    height: 100%;
  }
}

.home .content .recent-content {
  font-family: Cantarell, sans-serif;
}

.home .content .recent-content .recent-content-list {
  margin: 2em 0;
  list-style-type: none;
}

.home .content .recent-content .recent-content-item {
  align-items: center;
  margin-bottom: .4em;
  padding: .3em 0;
  font-size: 1.6em;
  line-height: 1.4;
  display: flex;
}

.home .content .recent-content .recent-content-item:before {
  content: "";
  width: 88px;
  height: 48px;
  background-position: 0 0;
  background-repeat: no-repeat;
  flex-shrink: 0;
  display: block;
}

.home .content .recent-content .content-type--blog:before {
  background-image: url("post.6dd5fefd.svg");
}

.home .content .recent-content .content-type--talks:before {
  background-image: url("talk.7e2d06c8.svg");
}

.home .content .recent-content .read-more-btn {
  margin: .7em 0;
  font-size: 1.3em;
}

@media (max-width: 990px) {
  .home .content {
    width: 60%;
  }
}

@media (max-width: 840px) {
  .home .content {
    width: 100%;
  }
}

a.sourceLine {
  pointer-events: none;
  color: inherit;
  text-decoration: inherit;
  line-height: 1.25;
  display: inline-block;
}

a.sourceLine:empty {
  height: 1.2em;
}

.sourceCode {
  overflow: visible;
}

code.sourceCode {
  white-space: pre;
  position: relative;
}

div.sourceCode {
  margin: 1em 0;
}

pre.sourceCode {
  margin: 0;
}

@media screen {
  div.sourceCode {
    overflow: auto;
  }
}

@media print {
  code.sourceCode {
    white-space: pre-wrap;
  }

  a.sourceLine {
    text-indent: -1em;
    padding-left: 1em;
  }
}

pre.numberSource a.sourceLine {
  position: relative;
  left: -4em;
}

pre.numberSource a.sourceLine:before {
  content: attr(title);
  text-align: right;
  vertical-align: baseline;
  pointer-events: all;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
  width: 4em;
  color: #aaa;
  border: none;
  padding: 0 4px;
  display: inline-block;
  position: relative;
  left: -1em;
}

pre.numberSource {
  border-left: 1px solid #aaa;
  margin-left: 3em;
  padding-left: 4px;
}

@media screen {
  a.sourceLine:before {
    text-decoration: underline;
  }
}

